(function ($) {
    $.fn.dropdowns = function (option) {

        // 
        var defaults = {
            toggleWidth: 768
        };

        // 
        var options = $.extend(defaults, option);

        // 
        var ww = document.body.clientWidth;

        // parend css
        var addParents = function () {
            $(".nav li a")
                .each(function () {
                    if ($(this)
                            .next().length > 0) {
                        $(this)
                            .addClass("parent");
                    }
                });
        };


        // 
        var adjustMenu = function () {

            // 
            if (ww < options.toggleWidth) {

                // 
                $(".toggleMenu")
                    .css("display", "inline-block");

                // 
                $("#site-navigation")
                    .removeClass("pure-menu-horizontal")
                    .addClass("pure-menu-vertical");

                // 
                if (!$(".toggleMenu")
                        .hasClass("active")) {
                    $(".nav")
                        .hide();
                } else {
                    $(".nav")
                        .show();
                }

                // 
                $(".nav li")
                    .unbind('mouseenter mouseleave');

                // 
                $(".nav li a.parent")
                    .each(function (e) {
                        // must be attached to anchor element to prevent bubbling
                        $(this)
                            .parent("li")
                            .addClass("pure-menu-active");
                    });

                // 
                $(".nav li a.parent")
                    .unbind('click')
                    .bind('click', function (e) {
                        // must be attached to anchor element to prevent bubbling
                        // e.preventDefault();
                        // $(this).parent("li").toggleClass("pure-menu-active");

                        // 
                    });
            }

            // 
            else if (ww >= options.toggleWidth) {

                //$(".pure-menu-children").addClass("animated").addClass("fadeInUp");

                // 
                $(".toggleMenu")
                    .css("display", "none");

                // 
                $("#site-navigation")
                    .addClass("pure-menu-horizontal")
                    .removeClass("pure-menu-vertical");

                // 
                $(".nav")
                    .show();

                // 
                $(".nav li a")
                    .unbind('click');

                $(".nav li")
                    .hover(function () {
                        $(this)
                            .find('ul:first')
                            .slideDown("fast")
                            .css({display: "block"});
                    }, function () {
                        $(this)
                            .find('ul:first')
                            .slideUp("fast")
                            .css({display: "none"});
                    });

            }
        };


        // 
        return this.each(function () {

            // 
            $(".toggleMenu")
                .click(function (e) {
                    e.preventDefault();
                    $(this)
                        .toggleClass("active");
                    $(this)
                        .next(".nav")
                        .toggle();
                    adjustMenu();
                });

            // 
            adjustMenu();

            // css
            addParents();

            // 
            $(window)
                .bind('resize orientationchange', function () {

                    // 
                    ww = document.body.clientWidth;
                    adjustMenu();
                });

        });

    };
})(jQuery);